<template>
  <div class="login">
    <!--  -->
    <div class="login-header">
      <van-nav-bar
        title=""
        right-text=""
        left-arrow
        :border="false"
        @click-left="goBackOrHome"
        @click-right="$router.push({ name: 'Home' })"
      />
      <!-- logo -->
      <img src="../assets/images/logo.jpg" />
      <div class="logo-title">Nice Mall</div>
      <div class="logo-desc">   </div>
    </div>

    <!--  -->
    <div class="login-info-box">
      <van-form>
        <van-field v-model="userInfo.phone" placeholder="" />
        <van-field
          v-model="userInfo.password"
          :type="isPwd ? 'password' : 'text'"
          placeholder=""
          :right-icon="isPwd ? 'closed-eye' : 'eye-o'"
          @click-right-icon="isPwd = !isPwd"
        />
        <div style="margin: 24px 0 0">
          <van-button round block color="#ff5777" @click="login"
            ></van-button
          >
          <van-button
            round
            block
            color="#fd8299"
            @click="isShowRegister = !isShowRegister"
            ></van-button
          >
        </div>
      </van-form>
    </div>

    <!--  -->
    <van-popup
      v-model="isShowRegister"
      position="bottom"
      class="register-box"
      closeable
    >
      <div class="register-title"></div>
      <van-form>
        <van-field
          v-model="userRegisterInfo.phone"
          name=""
          label=""
          placeholder="11"
        />
        <van-field
          v-model="userRegisterInfo.password"
          :type="isPwd ? 'password' : 'text'"
          name=""
          label=""
          placeholder=""
          :right-icon="isPwd ? 'closed-eye' : 'eye-o'"
          @click-right-icon="isPwd = !isPwd"
        />
        <van-field
          v-model="userRegisterInfo.nickName"
          name=""
          label=""
          placeholder=""
        />
        <div class="commit-btn">
          <van-button
            round
            block
            color="#ff5777"
            class="register-button"
            @click="register"
          >
            
          </van-button>
        </div>
      </van-form>
    </van-popup>
  </div>
</template>

<script>
// js
import { validForm } from "../assets/js/validForm.js";
export default {
  name: "Login",

  data() {
    return {
      // 
      isPwd: true,
      // 
      userInfo: {
        phone: "",
        password: "",
      },
      // 
      userRegisterInfo: {
        phone: "",
        password: "",
        nickName: "",
      },
      // 
      isShowRegister: false,
      // 
      userArr: [],

      // token
      _token: null,
    };
  },

  created() {
    if (localStorage.getItem("userArr")) {
      this.userArr = JSON.parse(localStorage.getItem("userArr"));
    }
    
    this._token = localStorage.getItem("_token");
  },

  methods: {
    // 
    register() {
      // 
      let o = {
        phone: {
          value: this.userRegisterInfo.phone,
          errorMsg: "",
          reg: /^1[3-9]\d{9}$/,
        },
        password: {
          value: this.userRegisterInfo.password,
          errorMsg: "(6-16)",
          reg: /^[A-Za-z]\w{5,15}$/,
        },
        nickName: {
          value: this.userRegisterInfo.nickName,
          errorMsg: "(1-10)",
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/,
        },
      };

      // validForm.valid(o)truefalse
      let isPass = validForm.valid(o);

      if (isPass) {
        // true 

        // this.userRegisterInfouserInfo
        const userInfo = { ...this.userRegisterInfo };
        /* 
        // ES6
        const userInfo = Object.assign({},this.userRegisterInfo);
        userInfo.appkey = this.appkey;
        */

        // 
        this.$toast.loading({
          // 
          message: "...",
          // 
          forbidClick: true,
          // 0
          duration: 0,
        });

        for (let i = 0; i < this.userArr.length; i++) {
          // 
          if (this.userArr[i].phone == userInfo.phone) {
            this.$toast("~~");
            return;
          } else if (this.userArr[i].nickName == userInfo.nickName) {
            this.$toast("~~");
            return;
          }
        }

        setTimeout(() => {
          this.userArr.push(userInfo);
          // 
          localStorage.setItem("userArr", JSON.stringify(this.userArr));
          // 
          this.isShowRegister = false;
          this.$toast("");
        }, 1000);
      }
    },

    // 
    login() {
      // 
      let o = {
        phone: {
          value: this.userInfo.phone,
          errorMsg: "",
          reg: /^1[3-9]\d{9}$/,
        },
        password: {
          value: this.userInfo.password,
          errorMsg: "(6-16)",
          reg: /^[A-Za-z]\w{5,15}$/,
        },
      };

      // validForm.valid(o)truefalse  
      let isPass = validForm.valid(o);

      if (isPass) {
        // true

        let userInfo = { ...this.userInfo };
        // 
        this.$toast.loading({
          // 
          message: "...",
          // 
          forbidClick: true,
          // 0
          duration: 0,
        });

        setTimeout(() => {
          for (let i = 0; i < this.userArr.length; i++) {
            if (
              this.userArr[i].phone == userInfo.phone &&
              this.userArr[i].password == userInfo.password
            ) {
              // token
              let _tokenArr = (userInfo.phone + userInfo.password).split("");
              let _token = _tokenArr
                .map((item) => {
                  return item.charCodeAt();
                })
                .join("");
              // token
              localStorage.setItem("_token", _token);
              // me
              this.$router.go(-1);
              this.$toast("~~");
              return;
            } else if (this.userArr[i].phone == userInfo.phone) {
              if (this.userArr[i].password != userInfo.password) {
                this.$toast("");
                return;
              }
            }
          }

          this.$toast("~~");
        }, 800);
      }
    },

    // 
    goBackOrHome(){
      if(this._token){
        this.$router.go(-1);
      }else{
        this.$router.push({name: "Home"});
      }
    }
  },
};
</script>

<style lang="less" scoped>
.login {
  background-color: #f5f5f5;
  height: 100vh;
  .login-header {
    /deep/ .van-nav-bar__title {
      color: #ff5777;
    }
    /deep/ .van-nav-bar .van-icon {
      color: #ff5777;
    }
    /deep/ .van-nav-bar__text {
      color: #ff5777;
    }
    /deep/ .van-nav-bar {
      background-color: #f5f5f5;
    }

    text-align: center;

    img {
      width: 90px;
      height: 90px;
      border-radius: 50%;
      margin-top: 10px;
    }

    .logo-title {
      font-weight: bold;
      font-size: 18px;
      color: #000;
    }
    .logo-desc {
      font-size: 12px;
      transform: scale(0.8);
      margin-top: -2px;
    }
  }

  .login-info-box {
    padding: 16px;
    width: 100%;
    box-sizing: border-box;

    /deep/ .van-form {
      border-radius: 6px;
      overflow: hidden;
    }
    /deep/ .van-cell:nth-of-type(2) {
      border-radius: 0 0 6px 6px;
    }
    /deep/ .van-cell {
      padding: 16px;
    }

    /deep/ .van-button--block {
      height: 42px;
    }
    /deep/ .van-button--block {
      margin-bottom: 50px;
    }
  }

  .register-box {
    padding: 15px 0 0;
    .register-title {
      font-size: 24px;
      color: #646566;
      font-weight: bold;
      padding: 0 15px;
    }
    .van-form {
      margin-top: 15px;
    }
  }

  .register-button {
    margin-top: 12px;
  }
}
</style>